@supports (display: grid) {
  @media screen and (max-width: 1000px) {
    .itinerary_photo {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 75vw;
    }
  }
  @media screen and (min-width: 1000px) {
    .itinerary_photo {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 75vw;
    }
  }
}

.photo {
  width: 4vw;
  height: 4vw;
}

.itinerary {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(10, auto);
  row-gap: 1vw;
  grid-template-areas:
    "title title"
    "img_church title_church"
    "img_church place_church"
    "img_church time_church"
    "img_venue title_venue"
    "img_venue place_venue"
    "img_venue time_venue"
    "img_party title_party"
    "img_party time_party";
}

.itinerary > div {
  justify-content: center;
  align-items: center;
}

.title {
  grid-area: title;
}
.img_church {
  grid-area: img_church;
}
.title_church {
  grid-area: title_church;
}
.place_church {
  grid-area: place_church;
}
.time_church {
  grid-area: time_church;
}
.img_venue {
  grid-area: img_venue;
}
.title_venue {
  grid-area: title_venue;
}
.place_venue {
  grid-area: place_venue;
}
.time_venue {
  grid-area: time_venue;
}
.img_party {
  grid-area: img_party;
}
.title_party {
  grid-area: title_party;
}
.place_party {
  grid-area: place_party;
}
.time_party {
  grid-area: time_party;
}
